	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2251628643\" title=\"View 'CIMG0460' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2254/2251628643_348ee4bb62.jpg\" alt=\"CIMG0460\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Happy Birthday to Me! I turn forty-six today, and I started the day by working the closing shift in the restaurant last night, getting home at 5:30. It was a good shift despite being one closer short.</p>\n<p>It was also one year ago today that Frauke returned from one of her many business trips to Munich to tell that that she'd be given the opportunity to become a franchisee, and would be home a lot more in the future.</p>","fields":{"title":"Forty-Six, and One","date":"09 Feb 2008","url":"https://papascott.de/2008-02-09-forty-six-and-one/"}}},"pathContext":{"slug":"/2008-02-09-forty-six-and-one/","prev":{"fields":{"title":"Pardon My Silence","date":"08 Feb 2008","slug":"/2008-02-08-pardon-my-silence/"}},"next":{"fields":{"title":"Valentine's Day","date":"14 Feb 2008","slug":"/2008-02-14-valentines-day/"}}}}

